<template>

  <form @submit.prevent="submit">
    <p>{{ pageHeader }}</p>
    <alert-success ref="alertSuccess"> {{ successMessage }} </alert-success>
    <alert-fail ref="alertFail"> {{ failMessage }} </alert-fail>

    <div class="form-group row">

      <label class="col-sm-2 col-form-label">App Name</label>
      <div class="col-sm-10">
        <input
          class="form-control"
          placeholder="Required"
          :state="fieldError.appName == null ? null: fieldError.appName.isCorrect"
          ref="appName"
          size="sm"
          v-model="appName">
      </div>
    </div>
    <div class="form-group row">
      <label class="col-sm-2 col-form-label">Remark</label>
      <div class="col-sm-10">
        <input
          class="form-control"
          placeholder="(optional) No longer than 200 characters"
          v-model="remark">
      </div>
    </div>

    <div class="row justify-content-center">
      <button class="btn btn-outline-secondary" @click="cancel" style="margin-right: 10px">Back</button>
      <button class="btn btn-outline-primary" :disabled="!isActive">{{ submitButtonName }}</button>
    </div>

  </form>

</template>

<script>
import AlertSuccess from '@/components/AlertSuccess';
import AlertFail from '@/components/AlertFail';

export default {
  data () {
    return {
      appName: '',
      remark: '',
      successMessage: '',
      failMessage: '',
      fieldError: {}
    };
  },
  props: {
    pageHeader: String,
    submitButtonName: String,
    isActive: Boolean,
    id: String
  },
  components: {
    AlertSuccess,
    AlertFail
  },
  methods: {
    submit () {
      this.fieldError = {};
      this.$refs.alertFail.hide();
      this.$emit('submit', { appName: this.appName, remark: this.remark });
    },
    cancel () {
      this.$router.push({ name: 'home' });
    },
    showSuccess (message) {
      this.successMessage = message;
      this.$refs.alertSuccess.countDownChanged(3);
    },
    showFail (message) {
      this.failMessage = message;
      this.$refs.alertFail.show();
    },
    getApp () {
      const url = process.env.VUE_APP_SERVICE_HOST + '/app/' + this.id;
      this.axios.get(url)
        .then(response => {
          const data = response.data;
          this.appName = data.name;
          this.remark = data.remark;
        })
        .catch(error => {
          this.showFail(`fail to get app from url : ${url} ${error.message} `);
        });
    },
    setDefault () {
      this.appName = '';
      this.remark = '';
      this.fieldError = {};
    },
    setFieldError (fields) {
      if (fields) {
        for (const field of fields) {
          this.fieldError[field.param] = { message: field.msg, isCorrect: false };
        }
      }
    }
  },
  watch: {
  },
  mounted () {
    if (this.id) {
      this.getApp();
    } else {
      this.$refs.appName.focus();
    }
  }
};
</script>

