<template>
  <div>
    <b-alert
      :show="dismissCountDown"
      variant="success"
      @dismiss-count-down="countDownChanged"
    >
      <p><slot></slot>, redirect in {{ dismissCountDown }} seconds...</p>
      <b-progress
        variant="success"
        :max="dismissSecs"
        :value="dismissCountDown"
        height="2px"
      ></b-progress>
    </b-alert>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dismissCountDown: 0,
      dismissSecs: 3,
      item: 0,
      showDismissibleAlert: false
    };
  },
  methods: {
    countDownChanged (dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    }
  },
  watch: {
    dismissCountDown (value) {
      if (value === 0) {
        this.$router.go(-1);
      }
    }
  }
};
</script>
